button {
    background: none;
    border: 2px solid;
    font: inherit;
    line-height: 1;
    margin: 0.5em;
    padding: 1em 2em;
  }


.slide:hover{
    background: white;
    color: black;
    border: 2px white solid
}


.avatar {
    padding: 2em;
    vertical-align: middle;
    width: 250px;
    height: 250px;
    border-radius: 50%;
}

.aboutme{
    font-family: 'Space Mono', monospace;
    font-weight: 700;
    font-size: 30px;
    color: white;
    padding: 0.5em;
}

@media (min-width: 768px) 
{
    .title {
        font-family: 'Space Mono', monospace;
        font-weight: 700;
        font-size: 90px;
    }
    
    .content {
        font-family: 'Space Mono', monospace;
        font-weight: 400;
        font-size: 20px;
    }

}

